body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f5f5f5;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 0 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px 3px 40px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__id {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__revard {
  justify-content: space-between;
  height: 12px;
  margin-top: 8px;
  display: flex;
}

.card__stars {
  margin-left: 2px;
  display: flex;
}

.card__star {
  box-sizing: border-box;
  background-image: url("star-active.edda4c43.svg");
  background-size: cover;
  width: 12px;
  height: 12px;
  margin-right: 8px;
}

.card__star:last-child {
  background-image: url("star.822d561e.svg");
}

.card__review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
}

.card__price {
  justify-content: space-between;
  margin-top: 26px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-bottom: 17px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 1px 2px #f5f5f5;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  text-decoration: none;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__list :last-child {
  margin: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  text-decoration: none;
}

.logo {
  width: 40px;
  height: 40px;
  margin-top: 4px;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.grid {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 200px 200px 200px 200px;
  gap: 48px 46px;
  width: 940px;
  margin: auto;
  display: grid;
}

@media screen and (width <= 900px) {
  .grid {
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 200px 200px 200px;
    width: 700px;
  }
}

@media screen and (width <= 600px) {
  .grid {
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-columns: 200px 200px;
    width: 500px;
  }
}

@media screen and (width <= 300px) {
  .grid {
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: 200px;
    width: 300px;
    margin: auto;
  }
}

.main {
  padding-top: 51px;
}

